<template>
  <div class="container">
    <div class="searchs">
      <el-form :inline="true" :model="searchs" class="form-search">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="搜索">
              <el-input v-model="searchs.keyword_search_value" placeholder="姓名/手机号"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">查询</el-button>
              <el-button type="success" @click="createUser">新增</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="编号" align="center" />
      <el-table-column prop="uname" label="姓名" align="center" />
      <el-table-column prop="mobile" label="手机号" width="150" align="center" />
      <el-table-column prop="name" label="银行预留名" width="150" align="center" />
      <el-table-column prop="bankname" label="银行名" width="150" align="center" />
      <el-table-column prop="card" label="银行卡号" width="220" align="center" />
      <el-table-column prop="uptime_date" label="修改时间" width="170" align="center" />
      <el-table-column prop="addtime_date" label="添加时间" width="170" align="center" />
      <el-table-column align="center" label="操作" width="240">
        <template slot-scope="scope">
          <el-button size="small" @click="editItem(scope.row.id)" type="primary">编辑</el-button>
          <el-button @click="removeItem(scope.row)" type="danger" size="mini">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import Fetch from '../../utils/fetch'
import Fetch4 from '../../utils/fetch4'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      count: 0,
      page_curren: 1,
      searchs: {
        keyword_search_value: '',
      },
      tableData: [],
      loading: false,
    }
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    createUser() {
      this.$router.push('/member/bank/create')
    },
    FetchList() {
      this.loading = true
      Fetch('/user/bank/getlist', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    editItem(uid) {
      this.$router.push('/member/bank/edit/' + uid)
    },
    removeItem(row) {
      this.$confirm('是否确认删除此条数据?', undefined, {
        type: 'warning',
        callback: type => {
          if (type === 'confirm') {
            Fetch4('/user/bank/delete/' + row.id)
              .then(() => {
                Message.success('删除成功')
                this.FetchList()
              })
              .catch(() => Message.error('删除失败'))
          }
        },
      })
    },
  },
}
</script>
